import React, { Suspense, useState } from "react";
import "./AddNewMaterail.css";
import {
  GetAllBomListByProductCodeActive,
  GetMaterialMasterDataQuery,
  GetMaterialMasterForBomQueryWithTagFilters,
  getChildrenTagsQuery,
  getMaterialTags,
  getParentTags,
} from "../../../api/graphQlQuery";
import { DTX_GRAPHQL_URL } from "../../../../config";
import { JWT_ERROR_MESSAGE } from "../../../constFunctions/constants";
import { useDispatch, useSelector } from "react-redux";
import { setNextPage } from "../../../redux-toolkit/slice/TaskpaneSlice";
import { logout } from "../../../constFunctions/copmponentFunctions";
import { getLastUsedRowNumber, getactiveSheetName, writeMaterilToExcel } from "../../../constFunctions/excelFunction";
import { useEffect } from "react";
const SelectLatestBom = React.lazy(() => import("../SelectLatestBom/SelectLatestBom"));
import { runQuery } from "../../../api/apiCalls";
import { setSingleMaterialDataInitial } from "../../../redux-toolkit/slice/action";
import SearchIcon from "../../../../../assets/icons/search-icon.svg";
import CloseIcon from "../../../../../assets/icons/x-circle.svg";
import CloseTag from "../../../../../assets/icons/close-icon.svg";
import { TagTrie } from "../../../constFunctions/tagHelpers";

function AddNewMaterail({ setAddNewBomMaterial }) {
  const { bomMappingData } = useSelector((state) => state.taskpane);
  const [searchKeyword, setSearchKeyword] = useState("");
  const [allMaterialData, setAllMaterialData] = useState([]);
  const [totalPage, setTotalPage] = useState();
  const [pageNumber, setPageNumber] = useState(1);
  const [associatedBom, setAssociatedBom] = useState([]);
  const [tagOptions, setTagOptions] = useState([]);
  const [savedTags, setSavedTags] = useState([]);
  const [tagTrie] = useState(new TagTrie());

  const dispatch = useDispatch();

  useEffect(() => {
    if (savedTags?.length > 0) {
      getChildrenTags();
    } else {
      getParentData("");
    }
  }, [savedTags]);
  const handleSearchQueryChange = (e) => {
    setSearchKeyword(e.target.value);
    if (e.target?.value?.length) {
      getMaterialData();
    }
  };
  const getMaterialData = () => {
    const variables = {
      jump: (pageNumber - 1) * 10,
      perPage: 10,
      searchKeyword: `%${searchKeyword}%`,
    };

    const token = localStorage.getItem("tenantToken");
    try {
      const body = {
        operationName: "GetMaterialMasterDataQuery",
        query: GetMaterialMasterDataQuery,
        variables: variables,
      };
      fetch(DTX_GRAPHQL_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(body),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              }
            });
          } else {
            setAllMaterialData(data?.data?.core_material_master);
            setTotalPage(Math.ceil(data?.data?.core_material_master_aggregate?.aggregate?.count / 8));
          }
        })
        .catch((error) => {
          console.log(error.message, "error.message");
        });
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    if (savedTags?.length > 0) {
      console.log(savedTags?.length);
      getMaterialDataWithTags();
    } else {
      getMaterialData();
    }
    getMaterialData();
  }, [pageNumber, searchKeyword, savedTags]);
  const writeBomMaterialToExcel = async (materialData) => {
    try {
      if (materialData?.material_product_code?.id) {
        const body = {
          operationName: "GetAllBomListByProductCodeActive",
          query: GetAllBomListByProductCodeActive,
          variables: {
            product_code: materialData?.material_product_code?.id,
          },
        };
        runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
          .then(async (data) => {
            if (data?.errors) {
              data.errors.map((element) => {
                if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                  logout();
                  dispatch(setNextPage("homeLogin"));
                }
              });
            } else {
              setAssociatedBom(data?.data.core_bom);
            }
          })
          .catch((error) => {
            console.log("someThing went wrong");
            // dispatch(setUploadLoader(false));
          });
      } else {
        const lastUsedRow = await getLastUsedRowNumber();
        const sheetName = await getactiveSheetName();
        writeMaterilToExcel(sheetName, lastUsedRow + 1, materialData, bomMappingData);
      }
    } catch (error) {
      console.log(error);
      // dispatch(setUploadLoader(false));
    }
  };
  const activeTagbox = () => {
    document.getElementById("tag-option-box").style.display = "block";
  };
  const deactiveTagBox = () => {
    document.getElementById("tag-option-box").style.display = "none";
  };
  const getParentData = (value) => {
    try {
      const body = {
        operationName: "getParentTags",
        query: getParentTags,
        variables: {
          value: value,
        },
      };
      runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              }
            });
          } else {
            setTagOptions(data?.data?.tag);
          }
        })
        .catch((error) => {
          console.log(error.message, "error.message");
        });
    } catch (error) {
      console.log(error);
    }
  };
  const getChildrenTags = () => {
    try {
      const body = {
        operationName: "getChildrenTagsQuery",
        query: getChildrenTagsQuery,
        variables: {
          value: "",
          parent_id: savedTags?.at(-1)?.id,
        },
      };
      runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              }
            });
          } else {
            setTagOptions(data?.data?.tag);
          }
        })
        .catch((error) => {
          console.log(error.message, "error.message");
        });
    } catch (error) {
      console.log(error);
    }
  };
  const addtoTag = (tag) => {
    deactiveTagBox();
    if (tag) {
      console.log(tag, "tag");
      tagTrie.insertTag({ name: tag.name, id: tag.id, parentId: tag.parent_id });
      // this.suggestions = [];
      // this.value = "";
      console.log(tagTrie.getTagArray());
      let tags = tagTrie.getTagArray();
      setSavedTags(tags);
      console.log(savedTags);
    }
  };
  const getMaterialDataWithTags = () => {
    try {
      const body = {
        operationName: "getMaterialTags",
        query: getMaterialTags,
        variables: {
          tags: savedTags.map((element) => {
            return element?.id;
          }),
        },
      };
      runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              }
            });
          } else {
            const body = {
              operationName: "GetMaterialMasterForBomQueryWithTagFilters",
              query: GetMaterialMasterForBomQueryWithTagFilters,
              variables: {
                jump: (pageNumber - 1) * 10,
                perPage: 10,
                searchKeyword: `%${searchKeyword}%`,
                materialIds: data.data.tag_material.map((element) => {
                  return element?.material_id;
                }),
              },
            };
            runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
              .then((data) => {
                if (data.errors) {
                  data.errors.map((element) => {
                    if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                      logout();
                      dispatch(setNextPage("homeLogin"));
                    }
                  });
                } else {
                  console.log(data, "getMaterialDataWithTags data");
                  setAllMaterialData(data?.data?.core_material_master);
                  setTotalPage(Math.ceil(data?.data?.core_material_master_aggregate?.aggregate?.count / 8));
                }
              })
              .catch((error) => {
                console.log(error.message, "error.message");
              });
          }
        })
        .catch((error) => {
          console.log(error.message, "error.message");
        });
    } catch (error) {
      console.log(error);
    }
  };
  // const removeTag = () => {};
  return (
    <div className="bom-addnew-material-container">
      <div>
        <div className="bom-search-bar-box">
          <span className="search-box">
            <img className="logo-img" src={SearchIcon} />
            <input
              type="search"
              className="bom-mat-search-input"
              placeholder="Search by name"
              value={searchKeyword}
              onChange={handleSearchQueryChange}
            />
          </span>
          <span className="close-btn" onClick={() => setAddNewBomMaterial(false)}>
            <img className="logo-img" src={CloseIcon} />
          </span>
        </div>
        {/* tags box  start */}
        <div className="bom-search-bar-box-tag">
          <span className="search-box-tag">
            <input
              type="search"
              className="bom-mat-search-input"
              placeholder="Tag name"
              // value={}
              // onChange={}
              onFocus={activeTagbox}
            />
          </span>
          <div className="tag-option-box" id="tag-option-box">
            <ul className="tag-option-ul" onMouseLeave={deactiveTagBox}>
              {tagOptions?.map((element, index) => (
                <li
                  onClick={() => {
                    addtoTag(element);
                  }}
                  key={index}
                >
                  {element?.name}
                </li>
              ))}
            </ul>
          </div>
          {savedTags?.map((element, index) => (
            <div className="tags" key={index}>
              <span>{element.name}</span>
              <img
                className="selected-tags-close"
                src={CloseTag}
                onClick={() => {
                  tagTrie.deleteTagById(element?.id);
                  setSavedTags(tagTrie.getTagArray());
                }}
              />
            </div>
          ))}
        </div>

        {/* tags end  */}
      </div>
      <ul>
        {allMaterialData?.map((element, index) => (
          <li
          className="material-list-li"
            onClick={() => {
              dispatch(setSingleMaterialDataInitial(element));
              writeBomMaterialToExcel(element);
            }}
            key={index}
          >
            {`${
              (element.custom_material_id.length > 6
                ? element.custom_material_id.substring(0, 6) + "..."
                : element.custom_material_id) || "no id "
            }`}
            -
            {`${
              (element.material_name.length > 30
                ? element.material_name.substring(0, 30) + "..."
                : element.material_name) || "No Name"
            }`}
          </li>
        ))}
      </ul>
      <div className="bom-addMaterial-pagination-parent">
        <div className="bom-addMaterial-pagination-box">
          {pageNumber > 3 && (
            <>
              <span className="pageNo" onClick={() => setPageNumber(1)}>
                1
              </span>
              <span>...</span>
            </>
          )}
          {pageNumber - 1 > 0 && (
            <span className="pageNo" onClick={() => setPageNumber(pageNumber - 1)}>
              {pageNumber - 1}
            </span>
          )}
          <span className="pageNo" onClick={() => setPageNumber(pageNumber)}>
            {pageNumber}{" "}
          </span>
          {pageNumber + 1 < totalPage && (
            <span className="pageNo" onClick={() => setPageNumber(pageNumber + 1)}>
              {pageNumber + 1}
            </span>
          )}
          {pageNumber < totalPage - 3 && (
            <>
              <span>...</span>
              <span className="pageNo" onClick={() => setPageNumber(totalPage)}>
                {totalPage}
              </span>
            </>
          )}
        </div>
      </div>

      {associatedBom?.length ? (
        <Suspense fallback={<div></div>}>
          <SelectLatestBom bomData={associatedBom} setAssociatedBom={setAssociatedBom} />
        </Suspense>
      ) : null}
    </div>
  );
}

export default AddNewMaterail;
